import { useState, useRef } from 'react';
import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, SelectControl, RangeControl, CheckboxControl, TextControl, ToggleControl, TabPanel, PanelRow, __experimentalUnitControl as UnitControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';
import SelectPure from 'select-pure';
import produce from 'immer';

// Settings Components
import { Label, Background, BColor, BDevice, BorderControl, BtnGroup, ColorsControl, HelpPanel, InlineMediaUpload, SelectPureControl, SpaceControl, Typography } from '../../Components';
import { AboutPro, SelectControlPro, BtnGroupPro } from '../../Components/Pro';
import { tabController } from '../../Components/utils/functions';
import { pxUnit, emUnit, vhUnit } from '../../Components/utils/options';

import { BControlPro } from '../bplugins_sdk/src/components/v1';
import { generalStyleTabs, layouts, categoriesPosition, effects, tickerDirections, imageFittings, aligns, dateFormats, postsOrdersBy, postsOrders, subLayouts, contentHeights, readMorePositions } from './utils/options';
import { omit, filterSelected, strToIntArr } from './utils/functions';

const Settings = ({ attributes, setAttributes, posts, postTypes, currentPostType, currentPostId, allTaxonomies, getTaxonomy, imageSizes }) => {
	const { isPremium, layout, subLayout, columns, columnGap, rowGap, isContentEqualHight, sliderHeight, content, postType, selectedTaxonomies, selectedCategories, selectedTags, isPostsPerPageAll, postsPerPage, postsOrderBy, postsOrder, postsOffset, postsInclude, postsExclude, isExcludeCurrent, isPagination, paginationPrevLabel, paginationNextLabel, paginationColors, paginationHovColors, paginationPadding, paginationSpacing, contentAlign, contentBG, contentPadding, border, sliderIsLoop, sliderIsTouchMove, sliderIsAutoplay, sliderAutoplayOptions, sliderSpeed, sliderEffect, sliderIsPage, sliderIsPageClickable, sliderIsPageDynamic, sliderPageColor, sliderPageWidth, sliderPageHeight, sliderPageBorder, sliderIsPrevNext, sliderPrevNextColor, tickerDirection, tickerSpeed, tickerInterval, tickerHeight, tickerVisible, tickerIsMousePause, elementsSort, isFImg, fImgSize, fImgFitting, isFImgLink, isTitle, isTitleLink, titleTypo, titleColor, titleMargin, isMeta, isMetaAuthor, isMetaAuthorLink, metaAuthorIcon, isMetaDate, metaDateFormat, metaDateIcon, isMetaCategory, metaCategoryIn, metaCategoryIcon, isMetaReadTime, metaReadTimeIcon, isMetaReadTimeSec, metaReadTimeLabel, isMetaComment, metaCommentIcon, metaTypo, metaTextColor, metaLinkColor, metaIconColor, metaColorsOnImage, metaMargin, isExcerpt, isExcerptFromContent, isEllipsisOnExcerpt, excerptLength, excerptAlign, excerptTypo, excerptColor, excerptMargin, isReadMore, readMorePosition, readMoreLabel, isLinkNewTab, readMoreAlign, readMoreTypo, readMoreColors, readMoreHovColors, readMorePadding, readMoreBorder } = attributes;

	const isGrid = 'grid' === layout;
	const isGrid1 = 'grid1' === layout;
	const isSlider = 'slider' === layout;
	const isTicker = 'ticker' === layout;

	const [device, setDevice] = useState('desktop');
	const [aboutProOpen, setAboutProOpen] = useState(false);
	const [selectedDateFormat, setSelectedDateFormat] = useState(dateFormats.find(f => f.value === metaDateFormat)?.value || 'custom');
	const tooltip = useRef(null);

	const isPosts = posts?.length;

	const handleShortcode = (e) => {
		const input = e.target;
		input.select();
		input.setSelectionRange(0, 30);
		document.execCommand('copy');
		tooltip.current.innerHTML = __('Copied Successfully!', 'advanced-post-block');
		setTimeout(() => {
			tooltip.current.innerHTML = __('Copy To Clipboard', 'advanced-post-block');
		}, 1500);
	};

	// Set max excerpt length
	// const [maxExcerptLength, setMaxExcerptLength] = useState(50);
	// useEffect(() => {
	// 	const allMaxExcerptLength = [];
	// 	posts?.map(post => {
	// 		allMaxExcerptLength.push(post?.excerpt?.replace(/(<p class='?"?read-more'?"?(.*?)<\/p>)/g, '').trim().split(' ').length);

	// 		setMaxExcerptLength(Math.max(...allMaxExcerptLength));
	// 	});
	// }, [posts]);

	const premiumProps = {
		isPremium,
		setOpen: setAboutProOpen
	};

	return <>
		<InspectorControls>
			<div className='apbInspectorInfo'>
				Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					{currentPostType === 'apb' && <PanelBody className='bPlPanelBody'>
						<Label className='mb5'>Copy the shortcode and use it anywhere.</Label>
						<PanelRow>
							<div className='apbFrontShortcode'>
								<input onClick={handleShortcode} value={`[apb id=${currentPostId}]`} />
								<span ref={tooltip} className='tooltip'>
									{__('Copy To Clipboard', 'advanced-post-block')}
								</span>
							</div>
						</PanelRow>
					</PanelBody>}


					<HelpPanel slug='advanced-post-block' docsLink='https://bplugins.com/docs/advanced-post-block' />


					<PanelBody className='bPlPanelBody' title={__('Layout', 'advanced-post-block')} initialOpen={true}>
						<Label className='mb5'>{__('Layout:', 'advanced-post-block')}</Label>
						<BtnGroupPro value={layout} onChange={val => {
							setAttributes({
								layout: val,
								subLayout: (['slider', 'ticker'].includes(val) && ['default', 'title-meta'].includes(subLayout)) || ('ticker' === val && 'overlay-half-content' === subLayout) ? 'left-image' : subLayout,
								columns: ['slider', 'ticker'].includes(val) ? { ...columns, desktop: 2 } : columns
							});
						}} options={layouts} isTextIcon={true} proValues={['grid1']} {...premiumProps} />

						<PanelRow className='mt20'>
							<Label className=''>{__('Sub Layout:', 'advanced-post-block')}</Label>
							<SelectControlPro value={subLayout} onChange={val => {
								setAttributes({ subLayout: val });
								'default' === val || 'title-meta' === val || 'left-image' === val || 'right-image' === val ? setAttributes({
									contentBG: { color: '#f4f2fc', styles: `background-color: #f4f2fc;` },
									titleColor: '#4527a4',
									metaCategoryIn: 'image',
									metaTextColor: '#333',
									metaLinkColor: '#8344c5',
									metaIconColor: '#4527a4',
									excerptColor: '#333'
								}) : 'overlay-content' === val || 'overlay-content-hover' === val || 'overlay-box' === val || 'overlay-content-box' === val ? setAttributes({
									contentBG: { color: '#000000b3', styles: `background-color: #000000b3;` },
									titleColor: '#ccc0f0',
									metaCategoryIn: 'content',
									metaTextColor: '#fff',
									metaLinkColor: '#e0d0f0',
									metaIconColor: '#ccc0f0',
									excerptColor: '#fff'
								}) : 'overlay-half-content' === val ? setAttributes({
									contentBG: { type: 'gradient', gradient: 'linear-gradient(0deg, #000000bf 0%, #0000 100%)', styles: `background-image: linear-gradient(0deg, #000000bf 0%, #0000 100%);` },
									titleColor: '#fff',
									metaCategoryIn: 'content',
									metaTextColor: '#fff',
									metaLinkColor: '#e0d0f0',
									metaIconColor: '#ccc0f0',
									excerptColor: '#fff'
								}) : '';

								'left-image' === val || 'right-image' === val ? setAttributes({
									columns: { ...columns, desktop: 2 }
								}) : setAttributes({
									columns: { ...columns, desktop: 3 }
								});

								'overlay-box' === val || 'overlay-content-box' === val ? setAttributes({
									contentAlign: 'center'
								}) : setAttributes({
									contentAlign: 'left'
								});
							}}
								options={
									isGrid1 ? subLayouts.filter(l => !['left-image', 'right-image'].includes(l.value)) :
										// isSlider ? subLayouts.filter(l => !['default', 'title-meta'].includes(l.value)) :
										isTicker ? subLayouts.filter(l => !['overlay-half-content'].includes(l.value)) :
											subLayouts
								}
								proValues={['overlay-content-box', 'overlay-half-content']}
								{...premiumProps}
							/>
						</PanelRow>
						<small>{__('Some settings may change when sub layout will be changed.', 'advanced-post-block')}</small>

						{!isGrid1 && !isTicker && <>
							<PanelRow className='mt20'>
								<Label className='mb5'>{__('Columns:', 'advanced-post-block')}</Label>
								<BDevice device={device} onChange={val => setDevice(val)} />
							</PanelRow>
							<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />
						</>}

						{!isTicker && <>
							<Label>{__('Column Gap:', 'advanced-post-block')}</Label>
							<RangeControl value={columnGap} onChange={val => setAttributes({ columnGap: val })} min={0} max={100} step={1} beforeIcon='arrow-right-alt' />
						</>}

						{!isSlider && <>
							<Label>{__('Row Gap:', 'advanced-post-block')}</Label>
							<RangeControl value={rowGap} onChange={val => setAttributes({ rowGap: val })} min={0} max={150} step={1} beforeIcon='arrow-down-alt' />
						</>}

						{(isGrid || isGrid1) && <ToggleControl className='mt20' label={__('Post Equal Height', 'advanced-post-block')} checked={isContentEqualHight} onChange={val => setAttributes({ isContentEqualHight: val })} />}

						{isSlider && <UnitControl className='mt20' label={__('Slider Min Height:', 'advanced-post-block')} labelPosition='left' value={sliderHeight} onChange={val => setAttributes({ sliderHeight: val })} units={[pxUnit(), emUnit(), vhUnit()]} />}

						<BControlPro className='mt20' label={__('Content/Texts Height:', 'advanced-post-block')} labelPosition='left' value={content?.height} onChange={val => setAttributes({ content: { ...content, height: val } })} options={contentHeights} {...premiumProps} Component={SelectControl} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Posts Query', 'advanced-post-block')} initialOpen={false}>
						<PanelRow>
							<Label className=''>{__('Post Type:', 'advanced-post-block')}</Label>
							<SelectControl value={postType} onChange={val => setAttributes({ postType: val, selectedTaxonomies: [] })} options={postTypes} />
						</PanelRow>

						{'post' === postType && getTaxonomy('category')?.length ? <>
							<Label>{__('Select Categories:', 'advanced-post-block')}</Label>
							<SelectPureControl
								value={filterSelected(getTaxonomy('category'), selectedCategories).map(cat => cat.toString())}
								onChange={val => setAttributes({ selectedCategories: val.map(cat => parseInt(cat)) })}
								options={getTaxonomy('category').map(cat => ({ label: cat.name, value: cat.id.toString() }))}
								SelectPure={SelectPure}
							/>
						</> : null}

						{'post' === postType && getTaxonomy('post_tag')?.length ? <>
							<BControlPro
								className='mt20'
								label={__('Select Tags:', 'advanced-post-block')}
								value={filterSelected(getTaxonomy('post_tag'), selectedTags).map(tag => tag.toString())}
								onChange={val => setAttributes({ selectedTags: val.map(tag => parseInt(tag)) })}
								options={getTaxonomy('post_tag').map(tag => ({ label: tag.name, value: tag.id.toString() }))}
								SelectPure={SelectPure} {...premiumProps} Component={SelectPureControl}
							/>
						</> : null}

						{allTaxonomies?.length ? <>
							<Label>{__('Select Taxonomies for filter:', 'advanced-post-block')}</Label>
							{allTaxonomies.map(tax => {
								const isInc = Object.keys(selectedTaxonomies).includes(tax.slug);

								return <BControlPro label={tax.name} key={tax.slug} checked={isInc} onChange={val => setAttributes({ selectedTaxonomies: val ? { ...selectedTaxonomies, [tax.slug]: [] } : omit(selectedTaxonomies, tax.slug) })} {...premiumProps} Component={CheckboxControl} />;
							})}
						</> : null}

						{allTaxonomies?.length && Object.keys(selectedTaxonomies)?.length ? Object.keys(selectedTaxonomies).map(tax => getTaxonomy(tax)?.length ? <BControlPro
							className='mt20'
							label={__(`Select ${allTaxonomies?.find(t => t.slug === tax)?.name}:`, 'advanced-post-block')}
							value={filterSelected(getTaxonomy(tax), selectedTaxonomies[tax]).map(tax => tax.toString())}
							onChange={val => setAttributes({ selectedTaxonomies: { ...selectedTaxonomies, [tax]: val.map(tax => parseInt(tax)) } })}
							options={getTaxonomy(tax).map(tax => ({ label: tax.name, value: tax.id.toString() }))}
							SelectPure={SelectPure} {...premiumProps} Component={SelectPureControl}
						/> : null) : null}

						<Label>{__('Post Per Page:', 'advanced-post-block')}</Label>
						<RangeControl value={postsPerPage} onChange={val => setAttributes({ postsPerPage: val, isPostsPerPageAll: -1 === val })} min={-1} max={36} step={1} />
						<small>{__('To show all posts set -1', 'advanced-post-block')}</small>

						<PanelRow className='mt20'>
							<Label className=''>{__('Post Order By:', 'advanced-post-block')}</Label>
							<SelectControl value={postsOrderBy} onChange={val => setAttributes({ postsOrderBy: val })} options={postsOrdersBy} />
						</PanelRow>

						<PanelRow className='mt20'>
							<Label className=''>{__('Post Order:', 'advanced-post-block')}</Label>
							<SelectControl value={postsOrder} onChange={val => setAttributes({ postsOrder: val })} options={postsOrders} />
						</PanelRow>

						{!isPostsPerPageAll && <>
							<BControlPro className='mt20' label={__('Post Offset:', 'advanced-post-block')} labelPosition='left' value={postsOffset} onChange={val => setAttributes({ postsOffset: parseInt(val) })} min={0} {...premiumProps} Component={NumberControl} />
							<small>{__('`Post Offset` will not work if `Post Per Page` is -1', 'advanced-post-block')}</small>
						</>}

						<BControlPro className='mt20' label={__('Include Posts:', 'advanced-post-block')} value={postsInclude?.join(',')} onChange={val => setAttributes({ postsInclude: strToIntArr(val) })} {...premiumProps} Component={TextControl} />
						<small>{__('Enter the posts id by coma separated Ex: `23,45,16`', 'advanced-post-block')}</small>

						<BControlPro className='mt20' label={__('Exclude Posts:', 'advanced-post-block')} value={postsExclude?.join(',')} onChange={val => setAttributes({ postsExclude: strToIntArr(val) })} {...premiumProps} Component={TextControl} />
						<small>{__('Enter the posts id by coma separated Ex: `23,45,16`', 'advanced-post-block')}</small>

						{currentPostType === postType && <BControlPro className='mt20' label={__('Exclude Current Post', 'advanced-post-block')} checked={isExcludeCurrent} onChange={val => setAttributes({ isExcludeCurrent: val })} {...premiumProps} Component={ToggleControl} />}
					</PanelBody>


					{!isSlider && !isTicker && !isPostsPerPageAll ? <PanelBody className='bPlPanelBody' title={__('Pagination', 'advanced-post-block')} initialOpen={false}>
						<BControlPro label={__('Show Pagination', 'advanced-post-block')} checked={isPagination} onChange={val => setAttributes({ isPagination: val })} {...premiumProps} Component={ToggleControl} />
						<small>{__(`Pagination will not work if, 'Show All Posts' is enable`, 'advanced-post-block')}</small>

						<BControlPro className='mt20' label={__('Previous Label:', 'advanced-post-block')} value={paginationPrevLabel} onChange={val => setAttributes({ paginationPrevLabel: val })} {...premiumProps} Component={TextControl} />

						<BControlPro className='mt20' label={__('Next Label:', 'advanced-post-block')} value={paginationNextLabel} onChange={val => setAttributes({ paginationNextLabel: val })} {...premiumProps} Component={TextControl} />

						<BControlPro label={__('Colors:', 'advanced-post-block')} value={paginationColors} onChange={val => setAttributes({ paginationColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} {...premiumProps} Component={ColorsControl} />

						<BControlPro label={__('Active & Hover Colors:', 'advanced-post-block')} value={paginationHovColors} onChange={val => setAttributes({ paginationHovColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} {...premiumProps} Component={ColorsControl} />

						<BControlPro className='mt20' label={__('Padding:', 'advanced-post-block')} value={paginationPadding} onChange={val => setAttributes({ paginationPadding: val })} defaults={{ vertical: '8px', horizontal: '15px' }} {...premiumProps} Component={SpaceControl} />

						<BControlPro className='mt20' label={__('Space Between:', 'advanced-post-block')} labelPosition='left' value={paginationSpacing} onChange={val => setAttributes({ paginationSpacing: val })} units={[pxUnit(), emUnit()]} {...premiumProps} Component={UnitControl} />
					</PanelBody> : ''}


					{isSlider && <PanelBody className='bPlPanelBody' title={__('Slider Options', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Enable Loop', 'advanced-post-block')} checked={sliderIsLoop} onChange={val => setAttributes({ sliderIsLoop: val })} />

						<ToggleControl className='mt10' label={__('Enable Touch Move', 'advanced-post-block')} checked={sliderIsTouchMove} onChange={val => setAttributes({ sliderIsTouchMove: val })} />

						<ToggleControl className='mt10' label={__('Enable Autoplay', 'advanced-post-block')} checked={sliderIsAutoplay} onChange={val => setAttributes({ sliderIsAutoplay: val })} />

						{sliderIsAutoplay && <>
							<Label className='mt10'>{__('Autoplay Delay (s):', 'content-slider-block')}</Label>
							<RangeControl value={sliderAutoplayOptions.delay} onChange={val => setAttributes({ sliderAutoplayOptions: { ...sliderAutoplayOptions, delay: val } })} min={0} max={10} step={.05} />
							<small>{__('Smaller delay value will be autoplay faster', 'content-slider-block')}</small>
						</>}

						<Label>{__('Speed (s):', 'advanced-post-block')}</Label>
						<RangeControl value={sliderSpeed} onChange={val => setAttributes({ sliderSpeed: val })} min={0} max={10} step={.05} />
						<small>{__('Smaller speed & delay value will be slide faster', 'advanced-post-block')}</small>

						<PanelRow className='mt20'>
							<Label className=''>{__('Effect:', 'advanced-post-block')}</Label>
							<SelectControl value={sliderEffect} onChange={cng => setAttributes({ sliderEffect: cng })} options={effects} />
						</PanelRow>
						<small>{__('To work fade & creative effects properly, set single column per view', 'advanced-post-block')}</small>

						<Label><strong>{__('Pagination:', 'advanced-post-block')}</strong></Label>
						<ToggleControl label={__('Show Pagination', 'advanced-post-block')} checked={sliderIsPage} onChange={val => setAttributes({ sliderIsPage: val })} />

						{sliderIsPage && <>
							<ToggleControl className='mt10' label={__('Enable Pagination Clickable', 'advanced-post-block')} checked={sliderIsPageClickable} onChange={val => setAttributes({ sliderIsPageClickable: val })} />

							<ToggleControl className='mt10' label={__('Enable Pagination Dynamic Bullets', 'advanced-post-block')} checked={sliderIsPageDynamic} onChange={val => setAttributes({ sliderIsPageDynamic: val })} />
						</>}

						<Label><strong>{__('Preview Next Button:', 'advanced-post-block')}</strong></Label>
						<ToggleControl label={__('Show Preview Next Button', 'advanced-post-block')} checked={sliderIsPrevNext} onChange={val => setAttributes({ sliderIsPrevNext: val })} />
					</PanelBody>}


					{isTicker && <PanelBody className='bPlPanelBody' title={__('Ticker Options', 'advanced-post-block')} initialOpen={false}>
						<BControlPro label={__('Direction:', 'advanced-post-block')} value={tickerDirection} onChange={val => setAttributes({ tickerDirection: val })} options={tickerDirections} {...premiumProps} Component={BtnGroup} />

						<BControlPro className='mt20' label={__('Speed:', 'advanced-post-block')} value={tickerSpeed} onChange={val => setAttributes({ tickerSpeed: val })}{...premiumProps} Component={TextControl} />
						<small>{__('Values: slow, medium, fast or any value in milliseconds.', 'advanced-post-block')}</small>

						<BControlPro className='mt20' label={__('Interval:', 'advanced-post-block')} labelPosition='left' value={tickerInterval} onChange={val => setAttributes({ tickerInterval: parseInt(val) })} {...premiumProps} Component={NumberControl} />
						<small>{__('Values: time in milliseconds.', 'advanced-post-block')}</small>

						<BControlPro className='mt20' label={__('Height:', 'advanced-post-block')} labelPosition='left' value={tickerHeight} onChange={val => setAttributes({ tickerHeight: val })} units={[pxUnit(), emUnit()]} {...premiumProps} Component={UnitControl} />
						<small>{__('Keep height 0, to auto height.', 'advanced-post-block')}</small>

						{'0px' === tickerHeight || '0em' === tickerHeight ? <>
							<BControlPro className='mt20' label={__('Post Visible:', 'advanced-post-block')} value={tickerVisible} onChange={val => setAttributes({ tickerVisible: val })} min={0} max={12} step={1} beforeIcon='visibility' {...premiumProps} Component={RangeControl} />
							<small>{__('0 to display all', 'advanced-post-block')}</small>
						</> : ''}

						<BControlPro className='mt20' label={__('Ticker Mouse Pause', 'advanced-post-block')} checked={tickerIsMousePause} onChange={val => setAttributes({ tickerIsMousePause: val })} {...premiumProps} Component={ToggleControl} />
					</PanelBody>}
				</>}


				{'elements' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Elements', 'advanced-post-block')}>
						<BControlPro label={__('Sort Element:')} value={elementsSort} onChange={val => setAttributes({ elementsSort: val })}{...premiumProps} Component={SortableControl} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Feature Image', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Show Feature Image', 'advanced-post-block')} checked={isFImg} onChange={val => setAttributes({ isFImg: val })} />

						{isFImg && <>
							<BControlPro className='mt20' label={__('Size:', 'advanced-post-block')} labelPosition='left' value={fImgSize} onChange={val => setAttributes({ fImgSize: val })} options={imageSizes} {...premiumProps} Component={SelectControl} />

							<SelectControl className='mt20' label={__('Fitting:', 'advanced-post-block')} labelPosition='left' value={fImgFitting} onChange={val => setAttributes({ fImgFitting: val })} options={imageFittings} />

							<ToggleControl className='mt10' label={__('Enable Link', 'advanced-post-block')} checked={isFImgLink} onChange={val => setAttributes({ isFImgLink: val })} />
						</>}
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Title', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Show Title', 'advanced-post-block')} checked={isTitle} onChange={val => setAttributes({ isTitle: val })} />

						{isTitle && <ToggleControl className='mt10' label={__('Enable Title Link', 'advanced-post-block')} checked={isTitleLink} onChange={val => setAttributes({ isTitleLink: val })} />}
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Meta Data', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Show Meta Data', 'advanced-post-block')} checked={isMeta} onChange={val => setAttributes({ isMeta: val })} />
						<hr />

						{isMeta && <>
							<Label className='mb10'><strong>{__('Author', 'advanced-post-block')}</strong></Label>

							<ToggleControl label={__('Show Author', 'advanced-post-block')} checked={isMetaAuthor} onChange={val => setAttributes({ isMetaAuthor: val })} />

							{isMetaAuthor && <>
								<BControlPro className='mt10' label={__('Author Link', 'advanced-post-block')} checked={isMetaAuthorLink} onChange={val => setAttributes({ isMetaAuthorLink: val })}{...premiumProps} Component={ToggleControl} />

								<BControlPro className='mt10' label={__('Author Icon:', 'advanced-post-block')} value={metaAuthorIcon} types={['image']} onChange={val => setAttributes({ metaAuthorIcon: val })} placeholder={__('Enter Author Icon URL', 'advanced-post-block')}{...premiumProps} Component={InlineMediaUpload} />
							</>}
							<hr />


							<Label className='mb10'><strong>{__('Date', 'advanced-post-block')}</strong></Label>

							<ToggleControl label={__('Show Date', 'advanced-post-block')} checked={isMetaDate} onChange={val => setAttributes({ isMetaDate: val })} />

							{isMetaDate && <>
								<Label className='mt10'>{__('Date Format:', 'advanced-post-block')}</Label>
								<PanelRow className='gap15'>
									<BControlPro value={selectedDateFormat} onChange={val => {
										setSelectedDateFormat(val);
										setAttributes({ metaDateFormat: 'custom' == val ? 'Y-m-d' : val });
									}} options={dateFormats} {...premiumProps} Component={SelectControl} />

									{'custom' === selectedDateFormat && <BControlPro value={metaDateFormat} onChange={val => setAttributes({ metaDateFormat: val })}{...premiumProps} Component={TextControl} />}
								</PanelRow>
								{'custom' === selectedDateFormat && <small>{__('Documentation on date formatting in ', 'advanced-post-block')} <a href='https://wordpress.org/support/article/formatting-date-and-time/' target='_blank' rel='noreferrer' aria-labe='Documentation on Date Formatting'>here</a>.</small>}

								<BControlPro className='mt10' label={__('Date Icon:', 'advanced-post-block')} value={metaDateIcon} types={['image']} onChange={val => setAttributes({ metaDateIcon: val })} placeholder={__('Enter Date Icon URL', 'advanced-post-block')} {...premiumProps} Component={InlineMediaUpload} />
							</>}
							<hr />


							<Label className='mb10'><strong>{__('Category', 'advanced-post-block')}</strong></Label>

							<ToggleControl label={__('Show Category', 'advanced-post-block')} checked={isMetaCategory} onChange={val => setAttributes({ isMetaCategory: val })} />

							{isMetaCategory && <>
								<BtnGroup label={__('Category In:', 'advanced-post-block')} value={metaCategoryIn} onChange={val => setAttributes({ metaCategoryIn: val })} options={categoriesPosition} />

								{'content' === metaCategoryIn && <BControlPro className='mt10' label={__('Category Icon:', 'advanced-post-block')} value={metaCategoryIcon} types={['image']} onChange={val => setAttributes({ metaCategoryIcon: val })} placeholder={__('Enter Category Icon URL', 'advanced-post-block')}{...premiumProps} Component={InlineMediaUpload} />}
							</>}
							<hr />


							<Label className='mb10'><strong>{__('Reading Time', 'advanced-post-block')}</strong></Label>

							<BControlPro label={__('Show Reading Time', 'advanced-post-block')} checked={isMetaReadTime} onChange={val => setAttributes({ isMetaReadTime: val })}{...premiumProps} Component={ToggleControl} />

							{(isMetaReadTime || !isPremium) && <>
								<BControlPro className='mt10' label={__('Show Reading Time Seconds', 'advanced-post-block')} checked={isMetaReadTimeSec} onChange={val => setAttributes({ isMetaReadTimeSec: val })}{...premiumProps} Component={ToggleControl} />

								<BControlPro className='mt10' label={__('Reading Time Label:', 'advanced-post-block')} value={metaReadTimeLabel} onChange={val => setAttributes({ metaReadTimeLabel: val })} placeholder={__('Reading Time Label')} {...premiumProps} Component={TextControl} />

								<BControlPro className='mt10' label={__('Reading Time Icon:', 'advanced-post-block')} value={metaReadTimeIcon} types={['image']} onChange={val => setAttributes({ metaReadTimeIcon: val })} placeholder={__('Enter Reading Time Icon URL', 'advanced-post-block')}{...premiumProps} Component={InlineMediaUpload} />
							</>}
							<hr />


							<Label className='mb10'><strong>{__('Comment', 'advanced-post-block')}</strong></Label>

							<ToggleControl label={__('Show Comment', 'advanced-post-block')} checked={isMetaComment} onChange={val => setAttributes({ isMetaComment: val })} />

							{isMetaComment && <BControlPro className='mt10' label={__('Comment Icon:', 'advanced-post-block')} value={metaCommentIcon} types={['image']} onChange={val => setAttributes({ metaCommentIcon: val })} placeholder={__('Enter Comment Icon URL', 'advanced-post-block')} {...premiumProps} Component={InlineMediaUpload} />}
						</>}
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Excerpt', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Show Excerpt', 'advanced-post-block')} checked={isExcerpt} onChange={val => setAttributes({ isExcerpt: val })} />

						{isExcerpt && <>
							<ToggleControl className='mt15' label={__('Show Excerpt from Content', 'advanced-post-block')} checked={isExcerptFromContent} onChange={val => setAttributes({ isExcerptFromContent: val })} />

							<BControlPro className='mt15' label={__('Show Ellipsis', 'advanced-post-block')} checked={isEllipsisOnExcerpt} onChange={val => setAttributes({ isEllipsisOnExcerpt: val })} {...premiumProps} Component={ToggleControl} />

							<Label className='mt15'>{__('Excerpt Length:', 'advanced-post-block')}</Label>
							<RangeControl value={excerptLength} onChange={val => setAttributes({ excerptLength: val })} min={0} max={120} step={1} />
							<small>{__('Excerpt max value will be your site default excerpt length', 'advanced-post-block')}</small>
						</>}
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Read More', 'advanced-post-block')} initialOpen={false}>
						<ToggleControl label={__('Show Read More', 'advanced-post-block')} checked={isReadMore} onChange={val => setAttributes({ isReadMore: val })} />

						{isReadMore && <>
							<BControlPro className='mt20' label={__('Position:', 'advanced-post-block')} labelPosition='left' value={readMorePosition} onChange={val => setAttributes({ readMorePosition: val })} options={readMorePositions} {...premiumProps} Component={SelectControl} />
							<small>{__(`For the bottom position, select 'Fill Available Space' option for the 'Content/Texts Height' from General->Layout`, 'advanced-post-block')}</small>

							<Label>{__('Read More Label:', 'advanced-post-block')}</Label>
							<TextControl value={readMoreLabel} onChange={val => setAttributes({ readMoreLabel: '' === val ? 'Read More' : val })} />

							<ToggleControl label={__('Open link in new tab', 'advanced-post-block')} checked={isLinkNewTab} onChange={val => setAttributes({ isLinkNewTab: val })} />
						</>}
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					{isPosts && <PanelBody className='bPlPanelBody' title={__('Content', 'advanced-post-block')}>
						<PanelRow>
							<Label className=''>{__('Text Align:', 'advanced-post-block')}</Label>
							<BtnGroup value={contentAlign} onChange={val => setAttributes({ contentAlign: val })} options={aligns} isIcon={true} />
						</PanelRow>

						<Background label={__('Background', 'advanced-post-block')} value={contentBG} onChange={val => setAttributes({ contentBG: val })} defaults={{ color: '#f4f2fc' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'advanced-post-block')} value={contentPadding} onChange={val => setAttributes({ contentPadding: val })} defaults={{ vertical: '20px', horizontal: '25px' }} />

						<BorderControl label={__('Border:', 'advanced-post-block')} value={border} onChange={val => setAttributes({ border: val })} defaults={{ radius: '5px' }} />
					</PanelBody>}


					{isPosts && isSlider && <PanelBody className='bPlPanelBody' title={__('Slider Options', 'advanced-post-block')} initialOpen={false}>
						{sliderIsPage && <>
							<BColor label={__('Pagination Bullets Color:', 'advanced-post-block')} value={sliderPageColor} onChange={val => setAttributes({ sliderPageColor: val })} defaultColor='#4527a4' />

							<UnitControl className='mt20' label={__('Pagination Width:', 'advanced-post-block')} labelPosition='left' value={sliderPageWidth} onChange={val => setAttributes({ sliderPageWidth: val })} units={[pxUnit(), emUnit()]} />

							<UnitControl className='mt20' label={__('Pagination Height:', 'advanced-post-block')} labelPosition='left' value={sliderPageHeight} onChange={val => setAttributes({ sliderPageHeight: val })} units={[pxUnit(), emUnit()]} />

							<BorderControl label={__('Pagination Border:', 'advanced-post-block')} value={sliderPageBorder} onChange={val => setAttributes({ sliderPageBorder: val })} defaults={{ radius: '50%' }} />
						</>}

						{sliderIsPrevNext && <BColor label={__('Preview Next Button Color:', 'advanced-post-block')} value={sliderPrevNextColor} onChange={val => setAttributes({ sliderPrevNextColor: val })} defaultColor='#4527a4' />}
					</PanelBody>}


					{isPosts && isTitle && <PanelBody className='bPlPanelBody' title={__('Title', 'advanced-post-block')} initialOpen={false}>
						<Typography value={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: { desktop: 25, tablet: 22, mobile: 20 } }} produce={produce} />

						<BColor label={__('Color:', 'advanced-post-block')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#4527a4' />

						<SpaceControl className='mt20' label={__('Margin:', 'advanced-post-block')} value={titleMargin} onChange={val => setAttributes({ titleMargin: val })} defaults={{ side: 4, bottom: '15px' }} />
					</PanelBody>}


					{isPosts && isMeta && <PanelBody className='bPlPanelBody' title={__('Meta Data', 'advanced-post-block')} initialOpen={false}>
						<Typography value={metaTypo} onChange={val => setAttributes({ metaTypo: val })} defaults={{ fontSize: { desktop: 13, tablet: 13, mobile: 13 }, textTransform: 'uppercase' }} produce={produce} />

						<BColor label={__('Text Color:', 'advanced-post-block')} value={metaTextColor} onChange={val => setAttributes({ metaTextColor: val })} defaultColor='#333' />

						<BColor label={__('Link Color:', 'advanced-post-block')} value={metaLinkColor} onChange={val => setAttributes({ metaLinkColor: val })} defaultColor='#8344c5' />

						<BColor label={__('Icon Color:', 'advanced-post-block')} value={metaIconColor} onChange={val => setAttributes({ metaIconColor: val })} defaultColor='#4527a4' />

						{'image' === metaCategoryIn && <ColorsControl label={__('Category Colors On Image:', 'advanced-post-block')} value={metaColorsOnImage} onChange={val => setAttributes({ metaColorsOnImage: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />}

						<SpaceControl className='mt20' label={__('Margin:', 'advanced-post-block')} value={metaMargin} onChange={val => setAttributes({ metaMargin: val })} defaults={{ side: 4, bottom: '15px' }} />
					</PanelBody>}


					{isPosts && isExcerpt && <PanelBody className='bPlPanelBody' title={__('Excerpt', 'advanced-post-block')} initialOpen={false}>
						<PanelRow>
							<Label className=''>{__('Text Align:', 'advanced-post-block')}</Label>
							<BtnGroup value={excerptAlign} onChange={val => setAttributes({ excerptAlign: val })} options={aligns} isIcon={true} />
						</PanelRow>

						<Typography value={excerptTypo} onChange={val => setAttributes({ excerptTypo: val })} defaults={{ fontSize: { desktop: 15, tablet: 15, mobile: 15 } }} produce={produce} />

						<BColor label={__('Color:', 'advanced-post-block')} value={excerptColor} onChange={val => setAttributes({ excerptColor: val })} defaultColor='#333' />

						<SpaceControl className='mt20' label={__('Margin:', 'advanced-post-block')} value={excerptMargin} onChange={val => setAttributes({ excerptMargin: val })} defaults={{ side: 4, bottom: '10px' }} />
					</PanelBody>}


					{isPosts && isReadMore && <PanelBody className='bPlPanelBody' title={__('Read More', 'advanced-post-block')} initialOpen={false}>
						<PanelRow>
							<Label className=''>{__('Button Align:', 'advanced-post-block')}</Label>
							<BtnGroup value={readMoreAlign} onChange={val => setAttributes({ readMoreAlign: val })} options={aligns.filter(a => a.value !== 'justify')} isIcon={true} />
						</PanelRow>

						<Typography value={readMoreTypo} onChange={val => setAttributes({ readMoreTypo: val })} defaults={{ fontSize: { desktop: 14, tablet: 14, mobile: 14 }, textTransform: 'uppercase', fontWeight: 600 }} produce={produce} />

						<ColorsControl value={readMoreColors} onChange={val => setAttributes({ readMoreColors: val })} defaults={{ color: '#fff', bg: '#8344c5' }} />

						<ColorsControl label={__('Hover Colors:', 'advanced-post-block')} value={readMoreHovColors} onChange={val => setAttributes({ readMoreHovColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />

						<SpaceControl className='mt20' label={__('Padding:', 'advanced-post-block')} value={readMorePadding} onChange={val => setAttributes({ readMorePadding: val })} defaults={{ vertical: '12px', horizontal: '35px' }} />

						<BorderControl label={__('Border:', 'advanced-post-block')} value={readMoreBorder} onChange={val => setAttributes({ readMoreBorder: val })} defaults={{ radius: '3px' }} />
					</PanelBody>}
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<AboutPro aboutProOpen={aboutProOpen} setAboutProOpen={setAboutProOpen} link='https://bplugins.com/products/advanced-post-block/#pricing'>
			<li>&emsp;<strong>{__('Layouts: ', 'advanced-post-block')}</strong>{__('Checkout more layouts and sub layouts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Tag Filter: ', 'advanced-post-block')}</strong>{__('Post filter by tags (only for default post).', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Custom Taxonomy Filter: ', 'advanced-post-block')}</strong>{__('Custom Taxonomy filter for post and custom posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Post Offset: ', 'advanced-post-block')}</strong>{__('Offset to exclude first N posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Include or Exclude Posts: ', 'advanced-post-block')}</strong>{__('Include or Exclude the posts by ids.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Exclude Current Post: ', 'advanced-post-block')}</strong>{__('If you use this block in a post and that post type is selected post then you can exclude the post.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Pagination: ', 'advanced-post-block')}</strong>{__('Add custom pagination bottom of the posts.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Sort Elements: ', 'advanced-post-block')}</strong>{__('Sort the title and meta elements.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Meta Data Icon: ', 'advanced-post-block')}</strong>{__('Custom icon for meta data.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Meta Author Link Toggle: ', 'advanced-post-block')}</strong>{__('Show meta author with or without link.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Reading Time: ', 'advanced-post-block')}</strong>{__('Show post reading time in meta area.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Excerpt from Content: ', 'advanced-post-block')}</strong>{__('Show excerpt from main content.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Ticker Layout Options: ', 'advanced-post-block')}</strong>{__('Options for ticker post layout.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Feature Image Size: ', 'advanced-post-block')}</strong>{__('Options to set feature image size.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Read More Position: ', 'advanced-post-block')}</strong>{__('Options to set the position fo Read More button.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Shortcode: ', 'advanced-post-block')}</strong>{__('Shortcode option to use anywhere.', 'advanced-post-block')}</li>

			<li>&emsp;<strong>{__('Update Post Query: ', 'advanced-post-block')}</strong>{__('Update Post Query using apb_query filter.', 'advanced-post-block')}</li>
		</AboutPro>


		<BlockControls>
			<AlignmentToolbar value={contentAlign} onChange={val => setAttributes({ contentAlign: val })} />
		</BlockControls>
	</>;
};
export default withSelect((select, { attributes }) => {
	const { getPostTypes, getTaxonomies, getEntityRecords } = select('core');
	const { getCurrentPostType, getCurrentPostId } = select('core/editor');

	const { postType } = attributes;
	const taxonomies = getTaxonomies({ per_page: -1 });

	return {
		postTypes: getPostTypes({ per_page: -1 })?.filter(p => !['apb', 'page', 'attachment', 'wp_block', 'wp_template', 'wp_navigation', 'wp_template_part', 'nav_menu_item'].includes(p.slug))?.map(({ name, slug }) => ({ label: name, value: slug })),

		currentPostType: getCurrentPostType(),

		currentPostId: getCurrentPostId(),

		allTaxonomies: 'post' === postType ? taxonomies?.filter(tax => tax.types.includes('post')).filter(tax => tax.slug !== 'category' && tax.slug !== 'post_tag') : taxonomies?.filter(tax => tax.types.includes(postType)),

		getTaxonomy: slug => getEntityRecords('taxonomy', slug, { per_page: -1 }),

		imageSizes: select('core/block-editor')?.getSettings()?.imageSizes?.map(s => ({ label: s.name, value: s.slug }))
	}
})(Settings);


import { SortableContainer, SortableElement, arrayMove } from 'react-sortable-hoc';

const SortableItem = SortableElement(({ value }) => <li className='bplSortableListItem'>{value}</li>);

const SortableList = SortableContainer(({ items }) => <ul>
	{items.map((value, index) => <SortableItem key={index} index={index} sortIndex={index} value={value} />)}
</ul>);

const SortableControl = ({ className = '', label = __('Sort:', 'bplugins'), value = [], onChange }) => {
	const onSortEnd = ({ oldIndex, newIndex }) => {
		onChange(arrayMove(value, oldIndex, newIndex))
	};
	return <div className={`bplSortableList ${className}`}>
		<Label className='mb5'>{label}</Label>

		<SortableList items={value} onSortEnd={onSortEnd} />

		<small>{__('Drag and drop to sort', 'bplugins')}</small>
	</div>;
}