import { useEffect, useRef } from 'react';

import MapPosts from '../../Layout/MapPosts';
import { setSliderHeight, sliderConfig } from '../../utils/configs';

const Slider = ({ posts, attributes, clientId }) => {
	const { sliderIsPage, sliderIsPrevNext } = attributes;

	const sliderRef = useRef(null);
	const sliderWrapperRef = useRef(null);

	const mainSl = `#apbAdvancedPosts-${clientId}`;

	useEffect(() => {
		const sliderWrapEl = sliderWrapperRef.current;
		if (sliderWrapEl) {
			// Re init slider
			sliderWrapEl.innerHTML = '';
			sliderWrapEl.innerHTML = sliderRef.current.outerHTML;

			new Swiper(`${mainSl} .apbSliderWrapper .apbSliderPosts`, sliderConfig(attributes));

			// Slider Height
			setSliderHeight(`apbAdvancedPosts-${clientId}`);
		}
	}, [attributes, posts]);


	return <>
		<div className='apbSliderWrapper' ref={sliderWrapperRef}></div>
		<div className='apbSliderPosts' ref={sliderRef}>
			<div className='swiper-wrapper'>
				<MapPosts posts={posts} attributes={attributes} />
			</div>
			{sliderIsPage && <div className='swiper-pagination'></div>}
			{sliderIsPrevNext && <>
				<div className='swiper-button-prev'></div>
				<div className='swiper-button-next'></div>
			</>}
		</div>
	</>
}
export default Slider;