import { useEffect, useRef } from 'react';
const $ = jQuery;

import MapPosts from '../../Layout/MapPosts';
import { tickerConfig } from '../../utils/configs';

const Ticker = ({ posts, attributes, clientId }) => {
	const tickerRef = useRef(null);
	const tickerWrapperRef = useRef(null);

	const mainSl = `#apbAdvancedPosts-${clientId}`;

	useEffect(() => {
		const tickerWrapEl = tickerWrapperRef.current;
		if (tickerWrapEl) {
			// Re init ticker
			tickerWrapEl.innerHTML = '';
			tickerWrapEl.innerHTML = tickerRef.current.outerHTML;

			$(`${mainSl} .apbTickerWrapper .apbTickerPosts`).easyTicker(tickerConfig(attributes));
		}
	}, [posts?.length, attributes, tickerWrapperRef]);

	return <>
		<div className='apbTickerWrapper' ref={tickerWrapperRef}></div>
		<div className='apbTickerPosts' ref={tickerRef}>
			<div>
				<MapPosts posts={posts} attributes={attributes} />
			</div>
		</div>
	</>;
}
export default Ticker;